body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: 'Montserrat', sans-serif;
  position: relative;
  z-index: -999;

  background: rgb(0, 0, 0);
  background: -moz-linear-gradient(270deg, rgba(0, 0, 0, 1) 0%, rgba(63, 63, 63, 1) 71%, rgba(80, 80, 80, 1) 100%);
  background: -webkit-linear-gradient(270deg, rgba(0, 0, 0, 1) 0%, rgba(63, 63, 63, 1) 71%, rgba(80, 80, 80, 1) 100%);
  background: linear-gradient(270deg, rgba(0, 0, 0, 1) 0%, rgba(63, 63, 63, 1) 71%, rgba(80, 80, 80, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#505050", GradientType=1);
  color: #fff;
}

#root {
  position: relative;
  z-index: -999;
}

a {
  text-decoration: none;
  color: white;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px none inset;
  transition: background-color 5000s ease-in-out 0s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}